import { BackProvider } from './../../providers/back/back';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController, LoadingController, ViewController } from 'ionic-angular';
import moment from 'moment'

@IonicPage()
@Component({
  selector: 'page-notifications',
  templateUrl: 'notifications.html',
})
export class NotificationsPage {

  private notis: any[] = []
  private uid: string = localStorage.getItem('uid')

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private back: BackProvider,
    private toast: ToastController,
    private load: LoadingController,
    private viewCtrl: ViewController
  ) {
  }

  ionViewDidLoad() {
    this.loadNotis()
  }

  reload(e) { this.loadNotis(() => e.complete()) }

  loadNotis(cb?: Function) {
    
    const l = this.load.create({content: 'Cargando notificaciones...'})
    l.present()

    this.back.getNotisUser(this.uid).subscribe(data => {
      this.notis = data
      l.dismiss()
      if (cb) cb()
    }, err => {
      l.dismiss()
      this.toast.create({message: 'No se pudieron cargar las notificaciones', duration: 2000}).present()
      this.viewCtrl.dismiss()
    })

  }

  setRead(id: string, i: number, read?: boolean) {

    if (read) return

    this.notis[i].read = true
    this.back.updateNoti(id, {read: this.notis[i].read}).subscribe(data => {
      console.log(data)
    }, err => {
      this.notis[i].read = read
      this.toast.create({message: 'No se pudo marcar como leída la notificación...', duration: 2000}).present()
    })

  }

  getDate(date: string) {
    return moment(date).fromNow()
  }

  handleClick(noti) {
    if(noti.hilo) this.navCtrl.push('HiloDetailPage', {id: noti.hilo.id})
  }

}



